import { init } from "ityped";
import React, { useRef, useEffect } from "react";
import AOS from "aos";

export const Header = (props) => {
  const textRef = useRef();

  useEffect(() => {
    init(textRef.current, {
      startDelay: 650,
      typeSpeed: 220,
      showCursor: false,
      strings: [" و نمونه کارها"],
    });
    AOS.init({
      duration: 2000,
    });
  }, []);

  return (
    <header id="header">
      <div className="intro">
        <div className="overlay">
          <div className="container">
            <div className="row">
              <div
                className="col-md-8 col-md-offset-2 col-xs-12 intro-text"
              >
                
                <h1>
                  {props.data ? props.data.title : "Loading"}
                  <span className="mobileP" ref={textRef}></span>
                </h1>
                <p>{props.data ? props.data.paragraph : "Loading"}</p>
                <a href="#about" className="btn btn-custom btn-lg page-scroll">
                  درباره من
                </a>{" "}
                <img src="img/air-balloon.png" className="header-image" alt="image"/>
              </div>
       
            </div>
          </div>
        </div>
      </div>
    </header>
  );
};
